form{
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: 25%;
    justify-content: center;
    align-items: center;
    background-color: white;
    padding: 1%;
    margin: 2.5% auto;
    border-radius: 5px;
    box-shadow: 0px 0px 30px 0px;
    position: relative;
}

.textInput{
    height: 30px;
    margin-bottom: 2%;
    width: 100%;
    border-radius: 5px;
    padding: 0.5%;
}

form button{
    height: 30px;
    width: 80px;
    margin-top: 2%;
    background-color: #eb4940;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 700;
}

form h1{
    padding: 1%;
}

.closeBtn{
    position: absolute;
    top: 0;
    right: 2%;
    z-index: 1;
    width: 30px;
}

@media only screen and (max-width:900px){
    form{
        width: 92%;
        margin-left: 2%;
        height: 85vh;
        padding: 0 2%;
    }

    .textInput{
        width: 100%;
    }
}