ul{
    background-color: white;
    width: 50%;
    margin-left: 25%;
    list-style: none;
    border-radius: 5px;
    text-align: center;
    margin-top: 2%;
    overflow-y: scroll;
    height: 85vh;
    margin-bottom: 2%;
}

ul::-webkit-scrollbar{
    width: 0;
}

li{
    border: 1px solid rgb(227, 224, 224);
    width: 80%;
    margin: 2% 0;
    margin-left: 10%;
}

p{
    display: flex;
    justify-content: space-between;
    margin: 1% 5%;
}

.analysis{
    justify-content: center;
}

p span{
    margin-right: 2%;
    font-weight: bold;
}

@media only screen and (max-width:900px){
    ul{
        width: 95%;
        margin-left: 2.5%;
    }

    li{
        width: 90%;
        margin-left: 5%;
    }
}