header{
    background-color: rgb(180, 175, 175);
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

header h2{
    margin-left: 2%;
}

nav{
    width: 50%;
    display: flex;
    justify-content: flex-end;
    padding-right: 5%;
}

header button{
    padding: 0.5%;
    border-radius: 5px;
    margin-left: 1%;
    cursor: pointer;
}