.option-box{
    width: 100%;
    display: flex;
    flex-direction: column;
    height: 120px;
    overflow-y:scroll;
    margin-left: 2%;
}

.option-box li{
    width: 90%;
    margin-left: 2.5%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 30px;
    padding: 1%;
    margin-bottom: 2%;
    border-bottom: 1px solid rgb(237, 235, 235);
    background-color: rgb(244, 240, 235);
}

.option-box input{
    transform: scale(1.5);
}